.user-menu {
  .list-none();
  font-size: 17px;

  @media (min-width: @tablet-width) {
    display: flex;
    align-items: center;
    position: relative;
    background-color: var(--white);
    padding-right: 50px;

    &::before {
      content: "";
      width: 100%;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: var(--gray);
    }
  }

  @media (min-width: @desktop-width) {
    padding-right: 0;

    &::before {
      height: 2px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 350px;
      width: calc(50vw - 526px);
      height: 100%;
      box-sizing: border-box;
      border-bottom: 2px solid var(--gray);
      background-color: var(--white);
    }
  }
}

.user-menu__item {
  border-bottom: 1px solid var(--gray);

  @media (min-width: @tablet-width) {
    border-bottom: none;
    flex: none;
  }
}

.user-menu__item--basket {
  @media (min-width: @tablet-width) {
    flex: auto;
  }
}

.user-menu__link {
  display: block;
  text-decoration: none;
  color: var(--black);
  padding: 29px 0 27px 76px;
  font-weight: bold;

  svg {
    opacity: 0.2;
  }

  &:hover {
    svg {
      opacity: 1;
      fill: var(--green);
    }

    .user-menu__span {
      opacity: 0.6;
    }
  }

  &:active {
    svg {
      opacity: 0.3;
      fill: var(--green);
    }

    .user-menu__span {
      opacity: 0.3;
    }
  }
}

.user-menu__span {
  @media (min-width: @tablet-width) {
    &--hidden {
      .visually-hidden();
    }
  }
}

.user-menu__search {
  position: relative;

  svg {
    position: absolute;
    left: 32px;

    @media (min-width: @tablet-width) {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  @media (min-width: @tablet-width) {
    width: 88px;
    box-sizing: border-box;
    padding: 44px 30px;

    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 100%;
      background-color: var(--gray);
      left: 0;
      top: 0;
    }

    &::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 100%;
      background-color: var(--gray);
      right: 0;
      top: 0;
    }
  }
}

.user-menu__basket {
  position: relative;

  svg {
    position: absolute;
    left: 32px;

    @media (min-width: @tablet-width) {
      position: static;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 35px 0 0 30px;
    min-height: 88px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    font-size: 17px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 28px;
  }
}
