.input {
  padding: 0;
  box-sizing: border-box;
  border: none;
  padding: 17px 0px 15px 0;
  width: 100%;
  border-bottom: 3px solid var(--black);
  font-size: 17px;
  line-height: 18px;
  outline: none;

  &:hover {
    border-bottom: 3px solid var(--green);
  }

  &:focus {
    border-bottom: 3px solid var(--green-hover);
  }

  @media (min-width: @tablet-width) {
    font-size: 19px;
    padding-left: 20px;
    padding-top: 14px;
    padding-bottom: 13px;

    &--back-gray {
      background-color: var(--gray-back-light);
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 15px;
    padding-left: 15px;
  }
}
