:root {
  font-size: 0.82353em;
  --green: #63D1BB;
  --green-dark: #56c3b2;
  --green-light: #59D5BC;
  --green-hover: #46C1AE;
  --shadow: #00010140;
  --black: #000000;
  --black-light: #444444;
  --gray: #ECECEC;
  --gray-light: #E5E5E5;
  --gray-back: #F9F9F9;
  --gray-back-light: #F1F1F1;
  --gray-dark: #C6C6C6;
  --gray-light-active: #B3B3B3;
  --white: #fff;
}
@media (min-width: 768px) {
  :root {
    font-size: 0.884em;
  }
}
body {
  margin: 0;
}
body::-webkit-scrollbar {
  width: 17px;
}
body::-webkit-scrollbar-track {
  background-color: var(--gray);
}
body::-webkit-scrollbar-thumb {
  background-color: var(--green);
  border-radius: 50px;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.container {
  width: auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0;
    max-width: 1050px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  color: var(--black-light);
  border: 2px solid var(--black-light);
  padding: 16px 0;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  box-sizing: border-box;
  transition: 150ms ease;
}
.button:hover {
  background-color: var(--black-light);
  color: var(--white);
}
.button:active {
  background-color: var(--black-light);
}
.button:active span {
  opacity: 0.3;
}
.button--secondary {
  background-color: var(--green);
  color: var(--white);
  border: none;
  padding: 18px 0;
  font-size: 17px;
}
.button--secondary:hover {
  background-color: var(--green);
  color: var(--white);
}
.button--secondary:active {
  background-color: var(--green);
}
.button--secondary:active span {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.input {
  padding: 0;
  box-sizing: border-box;
  border: none;
  padding: 17px 0px 15px 0;
  width: 100%;
  border-bottom: 3px solid var(--black);
  font-size: 17px;
  line-height: 18px;
  outline: none;
}
.input:hover {
  border-bottom: 3px solid var(--green);
}
.input:focus {
  border-bottom: 3px solid var(--green-hover);
}
@media (min-width: 768px) {
  .input {
    font-size: 19px;
    padding-left: 20px;
    padding-top: 14px;
    padding-bottom: 13px;
  }
  .input--back-gray {
    background-color: var(--gray-back-light);
  }
}
@media (min-width: 1150px) {
  .input {
    padding-top: 15px;
    padding-left: 15px;
  }
}
.page-body {
  font-family: "Open Sans", Arial, sans-serif;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr auto;
  grid-template-areas: "header" "main" "footer";
}
.page-body__header {
  background-color: var(--white);
  grid-area: header;
  height: auto;
  z-index: 1;
}
@media (max-width: 767px) {
  .page-body__header--height {
    height: 80px;
  }
}
.page-body__main {
  grid-area: main;
}
.page-body__footer {
  grid-area: footer;
}
@media (min-width: 768px) {
  .main-header {
    background-color: var(--gray-back);
    position: relative;
    overflow: hidden;
  }
  .main-header--gray-back {
    background-color: var(--gray-back-light);
  }
}
.main-header__container {
  padding: 0;
}
.navigation {
  position: relative;
  height: auto;
  background-color: var(--white);
}
@media (max-width: 767px) {
  .navigation--show {
    width: 100%;
    top: 0;
    position: relative;
    overflow-y: hidden;
    height: 80px;
  }
}
@media (min-width: 768px) {
  .navigation {
    position: relative;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: min-content 1fr;
    grid-template-areas: "logo user" "main parag";
    align-items: center;
    background-color: var(--white);
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "main logo user" "events . parag";
  }
}
.navigation__info {
  display: none;
  margin: 0;
  position: relative;
}
@media (min-width: 768px) {
  .navigation__info {
    grid-area: parag;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: end;
    width: 100%;
    height: 100%;
    font-size: 15px;
    color: var(--black-light);
    background-color: var(--gray-back);
    align-self: flex-start;
    padding-right: 50px;
  }
  .navigation__info--gray-back {
    background-color: var(--gray-back-light);
  }
}
@media (min-width: 1150px) {
  .navigation__info {
    padding-right: 0;
  }
}
@media (min-width: 768px) {
  .navigation__logo-burger {
    grid-area: logo;
  }
}
@media (min-width: 1150px) {
  .navigation__logo-burger {
    height: 100%;
  }
}
@media (min-width: 768px) {
  .navigation__main-menu {
    grid-area: main;
    align-self: flex-start;
    width: 100%;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .navigation__main-menu {
    position: static;
    width: 375px;
    height: 100%;
  }
}
@media (min-width: 768px) {
  .navigation__user-menu {
    grid-area: user;
    width: 351px;
    margin-left: auto;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .navigation__user-menu {
    align-self: flex-start;
  }
}
.navigation__events-menu {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__events-menu {
    grid-area: events;
    align-self: flex-start;
    width: 100%;
    height: 100%;
    grid-column: 1 / 3;
  }
}
.main-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  box-sizing: border-box;
  background-color: var(--white);
  font-size: 17px;
}
@media (min-width: 768px) {
  .main-menu {
    font-size: 15px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    background-color: var(--gray-back);
    padding-left: 50px;
    column-gap: 18px;
  }
  .main-menu--gray-back {
    background-color: var(--gray-back-light);
  }
}
@media (min-width: 1150px) {
  .main-menu {
    font-size: 17px;
    justify-content: space-between;
    align-self: flex-start;
    background: var(--white);
    padding-left: 0;
    border-bottom: 2px solid var(--gray);
  }
  .main-menu::before {
    content: "";
    position: absolute;
    top: 0;
    right: 375px;
    width: calc(50vw - 525px);
    height: 100%;
    border-bottom: 2px solid var(--gray);
    background-color: var(--white);
  }
  .main-menu::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: var(--gray);
    transform: rotate(90deg);
  }
}
.main-menu__link {
  display: block;
  text-decoration: none;
  color: var(--black);
  padding: 29px 0 27px 76px;
  font-weight: bold;
}
.main-menu__link:hover {
  opacity: 0.6;
}
.main-menu__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-menu__link {
    padding: 23px 0;
    text-align: start;
  }
}
@media (min-width: 1150px) {
  .main-menu__link {
    padding: 33px 0 32px 0;
  }
}
.main-menu__item {
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .main-menu__item {
    width: 44%;
    flex: auto;
    border-bottom: none;
  }
}
@media (min-width: 1150px) {
  .main-menu__item {
    flex-grow: 0;
    width: auto;
  }
}
.user-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 17px;
}
@media (min-width: 768px) {
  .user-menu {
    display: flex;
    align-items: center;
    position: relative;
    background-color: var(--white);
    padding-right: 50px;
  }
  .user-menu::before {
    content: "";
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: var(--gray);
  }
}
@media (min-width: 1150px) {
  .user-menu {
    padding-right: 0;
  }
  .user-menu::before {
    height: 2px;
  }
  .user-menu::after {
    content: "";
    position: absolute;
    top: 0;
    left: 350px;
    width: calc(50vw - 526px);
    height: 100%;
    box-sizing: border-box;
    border-bottom: 2px solid var(--gray);
    background-color: var(--white);
  }
}
.user-menu__item {
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .user-menu__item {
    border-bottom: none;
    flex: none;
  }
}
@media (min-width: 768px) {
  .user-menu__item--basket {
    flex: auto;
  }
}
.user-menu__link {
  display: block;
  text-decoration: none;
  color: var(--black);
  padding: 29px 0 27px 76px;
  font-weight: bold;
}
.user-menu__link svg {
  opacity: 0.2;
}
.user-menu__link:hover svg {
  opacity: 1;
  fill: var(--green);
}
.user-menu__link:hover .user-menu__span {
  opacity: 0.6;
}
.user-menu__link:active svg {
  opacity: 0.3;
  fill: var(--green);
}
.user-menu__link:active .user-menu__span {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .user-menu__span--hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.user-menu__search {
  position: relative;
}
.user-menu__search svg {
  position: absolute;
  left: 32px;
}
@media (min-width: 768px) {
  .user-menu__search svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (min-width: 768px) {
  .user-menu__search {
    width: 88px;
    box-sizing: border-box;
    padding: 44px 30px;
  }
  .user-menu__search::before {
    content: "";
    position: absolute;
    width: 2px;
    height: 100%;
    background-color: var(--gray);
    left: 0;
    top: 0;
  }
  .user-menu__search::after {
    content: "";
    position: absolute;
    width: 2px;
    height: 100%;
    background-color: var(--gray);
    right: 0;
    top: 0;
  }
}
.user-menu__basket {
  position: relative;
}
.user-menu__basket svg {
  position: absolute;
  left: 32px;
}
@media (min-width: 768px) {
  .user-menu__basket svg {
    position: static;
  }
}
@media (min-width: 768px) {
  .user-menu__basket {
    padding: 35px 0 0 30px;
    min-height: 88px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    font-size: 17px;
  }
}
@media (min-width: 1150px) {
  .user-menu__basket {
    padding-left: 28px;
  }
}
@media (min-width: 1150px) {
  .events-menu {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 15px;
    display: flex;
    position: relative;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 60px;
    background-color: var(--gray-back);
  }
  .events-menu--gray-back {
    background-color: var(--gray-back-light);
  }
}
.events-menu__link {
  display: block;
  text-decoration: none;
  color: var(--black);
  font-weight: bold;
  padding: 25px 0;
}
.events-menu__link:hover {
  opacity: 0.6;
}
.events-menu__link:active {
  opacity: 0.3;
}
.logo-burger {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 22px 30px;
  position: relative;
  background-color: var(--white);
  box-sizing: border-box;
  border-bottom: 2px solid var(--gray);
}
@media (min-width: 768px) {
  .logo-burger {
    display: block;
    padding: 22px 0 20px 38px;
  }
}
@media (min-width: 1150px) {
  .logo-burger {
    padding: 0;
    display: flex;
  }
}
.logo-burger__button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
.logo-burger__button--open::before {
  content: url("../img/icons/menu-open.svg");
  display: block;
  width: 20px;
  height: 14px;
}
.logo-burger__button--close::before {
  content: url("../img/icons/menu-close.svg");
  display: block;
  width: 21px;
  height: 20px;
}
@media (min-width: 768px) {
  .logo-burger__button {
    display: none;
  }
}
.logo-burger__main-link {
  display: block;
  width: 86px;
  height: 35px;
  margin: 0 auto;
  position: relative;
  left: 10px;
}
.logo-burger__main-link:hover {
  opacity: 0.6;
}
.logo-burger__main-link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .logo-burger__main-link {
    margin: 0;
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo-burger__main-link {
    width: 149px;
    height: 72px;
    margin: 0 auto;
    left: -13px;
  }
}
.promo {
  background-color: var(--gray-back);
  position: relative;
}
@media (min-width: 768px) {
  .promo::after {
    width: 100%;
    content: " ";
    position: absolute;
    bottom: -12px;
    background: url(../img/icons/zigzag-line-white.svg) repeat-x;
    height: 24px;
    background-size: auto 24px;
  }
}
@media (min-width: 1150px) {
  .promo::after {
    height: 34px;
    background-size: auto 36px;
    bottom: 0;
  }
}
@media (min-width: 1150px) {
  .promo::after {
    bottom: -15px;
  }
}
.promo__types-goods {
  width: 100%;
}
.promo__title {
  margin: 0 auto;
  text-align: center;
  font-size: 27px;
  padding: 0 30px;
  line-height: 30px;
  margin-bottom: 120px;
}
@media (min-width: 768px) {
  .promo__title {
