    flex-grow: 1;
  }
}
.user-data {
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .user-data {
    padding: 50px;
    padding-top: 45px;
  }
}
@media (min-width: 1150px) {
  .user-data {
    padding: 46px 175px 50px 62px;
  }
}
.user-data__block {
  display: flex;
  flex-wrap: wrap;
  gap: 38px;
}
@media (min-width: 768px) {
  .user-data__block {
    display: flex;
    flex-direction: column;
    gap: 22px;
  }
}
@media (min-width: 1000px) {
  .user-data__block {
    flex-direction: row;
    gap: 55px;
  }
}
@media (min-width: 1150px) {
  .user-data__block {
    flex-direction: column;
    row-gap: 18px;
  }
}
.user-data__label {
  flex-grow: 1;
}
@media (min-width: 768px) {
  .user-data__label {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.user-data__input {
  width: 100%;
  position: relative;
  display: block;
}
.user-data__input input {
  padding-right: 50px;
}
.user-data__input:hover svg {
  fill: var(--green);
  opacity: 1;
}
.user-data__input:focus-within svg {
  fill: var(--green-hover);
  opacity: 1;
}
@media (min-width: 768px) {
  .user-data__input {
    width: 75%;
  }
}
@media (min-width: 1150px) {
  .user-data__input {
    width: 700px;
  }
}
.user-data__phone {
  display: block;
  position: absolute;
  width: 25px;
  height: 24px;
  top: 50%;
  transform: translateY(-50%);
  right: 7px;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .user-data__phone {
    right: 26px;
  }
}
@media (min-width: 1150px) {
  .user-data__phone {
    right: 8px;
  }
}
.user-data__email {
  display: block;
  position: absolute;
  width: 32px;
  height: 21px;
  top: 50%;
  transform: translateY(-50%);
  right: 3px;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .user-data__email {
    right: 22px;
  }
}
@media (min-width: 1150px) {
  .user-data__email {
    right: 4px;
  }
}
.extra {
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .extra {
    padding: 50px 50px 51px 50px;
    display: flex;
    column-gap: 129px;
  }
}
@media (min-width: 1150px) {
  .extra {
    padding: 50px 0 46px 62px;
    column-gap: 75px;
  }
}
.extra__text {
  font-family: "Open Sans", Arial, sans-serif;
  width: 100%;
  height: 142px;
  border: 2px solid var(--black-light);
  padding: 17px 0 0 17px;
  box-sizing: border-box;
  margin-top: 23px;
  font-size: 17px;
  line-height: 18px;
  resize: none;
  outline: none;
}
.extra__text:hover {
  border: 2px solid var(--green);
}
.extra__text:focus {
  border: 2px solid var(--green-hover);
}
@media (min-width: 768px) {
  .extra__text {
    margin: 0;
    font-size: 19px;
    padding: 14px 0 0 14px;
  }
}
@media (min-width: 1150px) {
  .extra__text {
    width: 700px;
  }
}
@media (min-width: 768px) {
  .extra__line {
    margin-top: 17px;
  }
}
.line {
  display: block;
  position: relative;
}
@media (min-width: 768px) {
  .line {
    height: fit-content;
  }
}
.line::before {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  background-color: var(--green);
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .line::before {
    content: none;
  }
}
.line__title {
  font-weight: bold;
  margin: 0;
  color: var(--green);
  font-size: 17px;
  line-height: 16px;
  display: inline-block;
  position: relative;
  padding: 0;
  padding-right: 23px;
  background-color: var(--white);
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .line__title {
    padding: 0;
  }
  .line__title--back-gray {
    background-color: var(--gray-light-back);
  }
}
.popup {
  background-color: rgba(0, 0, 0, 0.5);
  animation-name: animatetop;
  animation-duration: 0.4s;
  display: none;
}
@keyframes animatetop {
  0% {
    top: -300px;
    opacity: 0;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}
.popup__content {
  background-color: var(--white);
  width: 100%;
  box-sizing: border-box;
  padding: 29px 30px 46px;
  border-top: 1px solid var(--gray-dark);
  box-shadow: 0px 5px 10px 0px var(--shadow);
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .popup__content {
    width: auto;
    margin: 0 101px;
    padding: 38px 106px 46px 106px;
  }
}
@media (min-width: 1150px) {
  .popup__content {
    width: 566px;
    margin: 0 auto;
  }
}
.popup__title {
  font-size: 25px;
  line-height: 31px;
  color: var(--black);
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  margin: 0 50px 14px 50px;
}
@media (min-width: 768px) {
  .popup__title {
    font-size: 30px;
    line-height: 57px;
    margin: 0;
    margin-bottom: 11px;
  }
}
.popup__button {
  width: 100%;
  margin-top: 28px;
}
.size {
  padding: 0;
  margin: 0;
  border: none;
}
.size__title {
  font-size: 15px;
  line-height: 30px;
  color: var(--black-light);
  text-align: center;
  margin-bottom: 21px;
}
@media (min-width: 768px) {
  .size__title {
    font-size: 17px;
    margin-bottom: 20px;
  }
}
.size__block {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px;
}
.size__label {
  display: flex;
  height: 52px;
  border: 2px solid var(--gray);
  box-sizing: border-box;
}
.size__label:hover {
  border: 2px solid var(--shadow);
}
.size__label:focus-within {
  border: 2px solid var(--green);
}
.size__control-mark {
  font-size: 17px;
  text-transform: uppercase;
  color: var(--black-light);
  font-weight: bold;
  margin: auto;
}
.footer {
  background-color: var(--gray-back-light);
  padding: 25px 0 23px 0;
}
@media (min-width: 768px) {
  .footer {
    padding: 51px 0 58px 0;
  }
}
@media (min-width: 768px) {
  .footer__social-list {
    width: 142px;
  }
}
.footer__icon-link {
  display: none;
}
@media (min-width: 768px) {
  .footer__icon-link {
    display: block;
    cursor: pointer;
  }
  .footer__icon-link svg {
    transition: 150ms ease;
  }
  .footer__icon-link svg:hover {
    fill: var(--green-hover);
  }
  .footer__icon-link svg:active {
    fill: var(--green-hover);
    opacity: 0.3;
  }
}
.footer__link-academy {
  display: flex;
  align-items: center;
  color: var(--black);
  font-size: 19px;
  line-height: 18px;
  text-decoration: none;
  font-weight: bold;
  margin-top: 24px;
  column-gap: 27px;
  justify-content: center;
}
.footer__link-academy svg {
  fill: var(--green);
  transition: 150ms ease;
}
.footer__link-academy:hover svg {
  fill: var(--green-hover);
}
.footer__link-academy:active svg {
  fill: var(--green-hover);
  opacity: 0.3;
}
@media (min-width: 768px) {
  .footer__link-academy {
    justify-content: flex-start;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .footer__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 29px;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 28px;
  border-bottom: 1px solid var(--gray-light);
}
@media (min-width: 768px) {
  .social-list {
    padding: 0;
    justify-content: flex-start;
    border: none;
  }
}
.social-list__link {
  display: block;
}
.social-list__link svg {
  transition: 150ms ease;
}
.social-list__link svg:hover {
  fill: var(--green-hover);
}
.social-list__link svg:active {
  fill: var(--green-hover);
  opacity: 0.3;
}
.social-list__icon {
  display: block;
}
